import {writable, derived} from 'svelte/store';
import axios from "axios"

function createStore() {
const {set,subscribe,update}= writable({
  status:"loading",
  blocks:[],
  pagination:[],
  page:10
});

async function getBlocks() {
  try {
    _update({blocks:[],status:"loading"})
    const {data} = await axios.get("http://localhost:3100/block");

    _update({blocks:data,status:"done", pagination:data.slice(0, 10)})
  } catch (error) {
    _update({blocks:[],status:"error"})
  }
}

async function getByHash(hash) {
  try {
    const {data} = await axios.get("http://localhost:3100/block/"+hash);
    console.log(data);
    return data
  } catch (error) {
    throw new Error("error occured.");
  }
}

function loadMore(page) {
  update(x=>({...x,page:(x.page+page), pagination:x.blocks.slice(0, x.page)}))
}


function _update(params={}) {
  update(x=>({...x, ...params}))
}
return {
  subscribe,
  getBlocks,
  getByHash,
  loadMore
}
}



export default createStore()